	function callMsgCode(){
		var countrycode = document.getElementsByTagName("html")[0].getAttribute("data-countrycode");
		if(!countrycode) return false;

		var msgJson = "/" + countrycode + "/" + "js/msg.json";
		function t(t) {
			$.ajax({
				type: "get",
				url: t,
				async: false,
				dataType: "json",
				success: function(e) {
					/* jshint ignore:start */
					typeof e == "string" && (e = jQuery.parseJSON(e)),
					commonMsg = e,
					errorMsg = e.ajaxerror,
					wtbMsg = e.wheretobuy,
					alertMsg = e.alert;
					/* jshint ignore:end */
				}
			});
		}
		t(msgJson);
	}
