	var smsModal = {
		el: document.getElementById("modal_sms_send"),
		form: document.getElementById("smsSendForm"),
		openBtn: document.querySelectorAll("[href='#modal_sms_send']"),
		hasCaptcha: !!$('#modal_sms_send').find('#customizedCaptcha').length,
		captchaText: $('#modal_sms_send').find('#captchaCodeTextBox'),
		content: {
			$inputCon: null,
			$completeCon: null
		},
		init: function(){
			smsModal.content.$inputCon = $(smsModal.el).find("[data-send-complete='false']");
			smsModal.content.$completeCon = $(smsModal.el).find("[data-send-complete='true']");
			smsModal.addEvent();
		},
		resetModalContent: function(){
			smsModal.content.$completeCon.hide();

			smsModal.content.$inputCon.find("input:not([type=hidden])").val("");
			smsModal.content.$inputCon.find(".field-block").removeClass("error").find(".error-msg span").hide();
			smsModal.content.$inputCon.show();
			smsModal.content.$inputCon.find(".js-scroll").mCustomScrollbar("update");
			if (!!customizedCaptcha){
				customizedCaptcha.ReloadImage();
			}
		},
		activeCompleteContent: function(result){
			var targetSelector = result ?  '.success' : '.failure';
			smsModal.content.$inputCon.hide();
			smsModal.content.$completeCon.show()
										 .find(targetSelector).show().siblings().hide();
		},
		setAjaxMsg: function(obj){
			for (var prop in obj) {
				var fieldBlock = $(smsModal.el).find("[name="+ prop +"]").closest(".field-block");
				fieldBlock.addClass("error").find(".ajax-msg").text(obj[prop]).show().siblings().hide();
			}
		},
		getSmsSendResult: function(){
			var resultUrl = $(smsModal.form).attr("data-result-url");
			if(!resultUrl) return false;

			var resultBool = false;
			var validateBool = false;
			var validateMsg;

			// get short url 
			var ajaxData = $(smsModal.form).data();
			if(ajaxData.paramName) {
				var shortUrlParam = ajaxData.paramName+"="+ajaxData.url;
				var ajaxUrl = 'https://www.lg.com/common/shorturl/getShortUrl.lgajax';

				if(ajaxData.getUrl) {
					ajaxUrl = ajaxData.getUrl;
				}
				$.ajax({
					type: "GET",
					timeout: 5e4,
					url: ajaxUrl,
					data: shortUrlParam,
					dataType: "jsonp",
					jsonp: "callback",
					success: function(data) {
						var shortUrlInput = $(smsModal.form).find('[name="shortUrl"]');
						shortUrlInput.val(data.shortUrl);
					}
				}).done(function(){
					if ( !!captcha && captcha.validation() ){
						$.ajax({
							type: "get",
							url: resultUrl,
							data: $(smsModal.form).serialize(),
							async: false,
							dataType: "json",
							success: function(data) {
								// data.result: true or false
								resultBool = data.result;
								// data.validate: true or false
								validateBool = data.validate;
								// valite error = data.error: { [inputName]: "errorMsg" }
								// result error = data.error: "ERROR errNum:015"
								validateMsg = !validateBool ? data.error : {};
							},
							error: function(request,status,error) {
								// console.log("status: "+status);
								// console.log("error: "+error);
								resultBool = false;
							}
						});

						if(validateBool){
							smsModal.activeCompleteContent(resultBool);
						}else{
							smsModal.setAjaxMsg(validateMsg);
						}

					}
				});
			}
		},
		addEvent: function(){
			$('#smsSendSubmit').on({
				click: function(e){
					e.preventDefault();
					if($(smsModal.form).checkValidation()){
						if ( smsModal.hasCaptcha ){
							if ( !!smsModal.captchaText.val() ){
								smsModal.getSmsSendResult();
							}
						} else {
							smsModal.getSmsSendResult();
						}
					}
				}
			});
			$(smsModal.openBtn).on({
				click: function(e){
					smsModal.resetModalContent();
				}
			});
		}
	};
	// add Common Support
	// ca_en postcode eventHandler
	$(document).on('input[data-postcode-type="ca_en"], input[data-postcode-type="ca_fr"]','keyup keypress paste', function( e ){
		var $this = $(this);
		//var key = e.keyCode ? e.keyCode : e.which
		var value = $this.val();
		$this.attr("maxlength", "7");
		if (value.charAt(3) != " " && value.length >= 6) {
			var s = value.substr(0, 3),
				o = value.substr(3, 3);
			$this.val(s + " " + o);
		}
		if ($this.val().length >= 7)
			return e.preventDefault(),
			!1;
		if (e.type == "paste") {
			var u = t.originalEvent.clipboardData.getData("text"),
				w = u.substr(0, 3),
				x = u.substr(3, 3);
			u.charAt(3) != " " && $this.val(w + " " + x);
		}
	});
	// masking
	$(document).on('masking', function() {
		/* LGEBR-75 20200604 modify */
		var $maskingTargets = $("[data-masked-type]");

		if ($maskingTargets.length) {
			if (!$.mask) {
				$.cachedScript( '/lg5-common-gp/library/jquery.maskedinput.min.js' ).done(function( script, textStatus ) {
					applyMaskInput($maskingTargets);
				});
			} else {
				applyMaskInput($maskingTargets);
			}
		}
		/* //LGEBR-75 20200604 modify */

		/* LGECI-217 20201015 add */
		isNumber = function(obj, strict) {
			var strict = strict === true ? true : false;
			if (strict) {
				return !isNaN(obj) && obj instanceof Number ? true : false;
			} else {
				return !isNaN(obj - parseFloat(obj));
			}
		}

		var $canadaPostal = $("[data-postcode-guest]"); // 비회원 제품등록 페이지 여부 확인

		if ($canadaPostal.length) {
			$.cachedScript( '/lg5-common-gp/library/jquery.inputmask.min.js' ).done(function( script, textStatus ) {
				$canadaPostal.inputmask("*** ***");
				$canadaPostal.on('change keyup keypress paste', function(e){
					var $this = $(this);
					var value = $this.val();
					if(isNumber(value.charAt(0)) || isNumber(value.charAt(1)) && isNumber(value.charAt(3)) && isNumber(value.charAt(4)) ){
						$(this).closest('.field-block').addClass('error');
						$(this).next('.error-msg').find('span').hide();
						$(this).next('.error-msg').find('.invalid-us').show();
					}else{
						$(this).closest('.field-block').removeClass('error');
						$(this).next('.error-msg').find('span').hide();
					}
				});
			});
		}
		/* //LGECI-217 20201015 add */

		/* LGEPL-342 20210315 add 
		** masking holder _ (언더바) string으로 인해 focus 시 커서가 맨 뒤에 위치하는 이슈 개선
		** 입력 완료했을 시엔 맨 뒤에 위치하는 게 맞음으로 last string validation 추가
		*/
		$('input[data-masked-type]').on('focus click', function(e){
			var postCodelValue = $(this).val(),
				lastIdx = parseInt(postCodelValue.length) - 1;
			if(postCodelValue.charAt(lastIdx) == '_') $(this)[0].setSelectionRange(0, 0);
		});
		/* //LGEPL-342 20210315 add */
		
		function applyMaskInput( $target ) {
			$target.each(function() {
				var type = $(this).attr("data-masked-type");
				switch (type) {
				case "BRMobile":
					// 20200406 START 구유정 || BRMobile mask 시점 click → focus 변경
					$(this).one("focus", function() {
					// 20200406 END 구유정 || BRMobile mask 시점 click → focus 변경
						$(this).mask("(99)99999-9999");
					}),
					$(this).attr("inputmode") == "numeric" && $(this).attr("type", "tel");
					break;
				case "AAA AAA":
					var postType = this.getAttribute('data-postcode-type');
					if ( postType == 'ca_en' || postType == 'ca_fr'){
						$(this).mask("AAA AAA");
					}				
					break;
				default:
					var r = $(this)[0].hasAttribute("masked-holder") ? $(this).attr("masked-holder") : type.replace(/0|9|A|\*/g, "_");
					$(this).mask(type, {
						placeholder: r
					});
				}
			});
		}
	});

