	if ( countryCode == 'br' ){
		$('[data-blur-action]').each(function(){
			var $this = $(this);
			var blurAction = $this.data('blurAction');


			if ( blurAction == 'cep' ){
				$this.on('blur', function(){
					var $this = $(this);
					var url = $this.data('url');
					var cep = $this.val();
					var cityTarget = $this.data('targetCity');
					var addressTarget = $this.data('targetAddress');
					var stateTarget = $this.data('targetState');
					// 20200325 START 김우람 || 3 PHASE BR nextdoor 추가 
					var nextDoorTarget = $this.data('nextdoor');
					
					if ( !url ) {
						return;
					}
					
					ajax.noCacheCall(url, {"postalCode" : cep }, 'json', function( res ){
						// 20200409 START 구유정 || CEP 관련 수정 (sign, mylg, email, request)
						if ( res.result ){
							if( cityTarget ){
								if ($('[name='+ cityTarget +']').length){
									$('[name='+ cityTarget +']').val( res.city || res.companyCity );
								}
								if ($('[name='+ addressTarget +']').length){
									$('[name='+ addressTarget +']').val( res.address || res.companyAddress );
								}
								if ($('[name='+ stateTarget +']').length){
									$('[name='+ stateTarget +']').val( res.state || res.companyState );
								}
								if ($('[name='+ nextDoorTarget +']').length){
									$('[name='+ nextDoorTarget +']').val( res.nextdoor || res.b2bNumComp );
								}							
							}else{
								delete res.result;
								for (var key in res) {
									$('#' + key).val(res[key]);
								}
							}
						/* LGEBR-535 : 20210708 add */
						}else{
							if ($('[name='+ cityTarget +']').length){
								$('[name='+ cityTarget +']').val('');
							}
							if ($('[name='+ addressTarget +']').length){
								$('[name='+ addressTarget +']').val('');
							}
							if ($('[name='+ stateTarget +']').length){
								$('[name='+ stateTarget +']').val('');
							}
							if ($('[name='+ nextDoorTarget +']').length){
								$('[name='+ nextDoorTarget +']').val('');
							}	
						}
						/*// LGEBR-535 : 20210708 add */
						// 20200409 END 구유정 || CEP 관련 수정 (sign, mylg, email, request)
					}, 'post', 'body');
					// 20200325 END 김우람 || nextdoor 추가 
				});
				
			}
		});
	}

	// 20200402 START 김우람 || placeholder 기능 추가 
	// RU select country to set Tel Number
	if ( countryCode == 'ru' ){
		$('[data-target-post]').chosen().change(function(){			
			var select = $(this);
			var selectedOption = select.find('option:selected');
			var postCode = selectedOption.data('post');
			var placeholderMsg = selectedOption.data('placeholder-msg');

			var telLength = selectedOption.data('maxLength');

			var telInputIds = select.data('targetTel').split(',');
			var postInputIds = select.data('targetPost').split(',');
			var telInputs = [];
			var postInputs = [];
			

			$.each( telInputIds, function(){
				//telInputs.push( $('[name="'+ this +'"]') );
				telInputs.push( $(this.toString()) );
			});

			$.each( postInputIds, function(){
				//postInputs.push( $('[name="'+ this +'"]') );
				postInputs.push( $(this.toString()) );
			});

			if (!postCode){
				return;
			}

			$.each(postInputs, function(){
				this.val(postCode);
			});

			$.each(telInputs, function(){
				this.val('');
				this.attr('minlength' , telLength);
				this.attr('maxlength' , telLength);
				this.attr('placeholder', placeholderMsg);
				this.attr('disabled', false);
			});
		});
	}
