	// use bootstrap Datepicker
	/* LGEBR-396 s*/
	initDatePicker = function(enabledDates) {
		var $datepicker = $('[data-provide="datepicker"]');
		$datepicker.each(function() {

			var $this = $(this),
				option = {
					orientation: 'bottom left',
					daysOfWeekHighlighted: '0',
					beforeShowDay: function (date) {
						if(enabledDates!= undefined && enabledDates.length){
							var thismonth = date.getMonth()+1;
							var thisday = date.getDate();
							if(thismonth<10){
								thismonth = "0"+thismonth;
							}
							if(thisday<10){
								thisday = "0"+thisday;
							}
							var allDates =  thisday+"/"+thismonth+"/"+date.getFullYear();
							if(enabledDates.indexOf(allDates) != -1){
								  if(date.getDay() != 0){ 
								  return true;
								  }else{
									  return false;
								  }
							  }else{
								  return false;
							  }
						   }
					}
				};
			
			if ($this.data('datepicker')||$this.hasClass('hasDatePicker'))
				return;
			
			// 200421 START 전호근 - format 수정
			$.extend(option, $this.data());
			($this.data('format').match(/y/g).length == 2) && (option.format = $this.data('format').replace('yy', 'yyyy'));
			if ($('html').data('countrycode') == 'br') {
				$.fn.datePicker.dates['en'].months = ["Janeiro", "Fevereiro", "Março", "Abril", "Maio", "Junho", "Julho", "Agosto", "Setembro", "Outubro", "Novembro", "Dezembro"];
				$.fn.datePicker.dates['en'].monthsShort = ["Jan", "Fev", "Mar", "Abr", "Mai", "Jun", "Jul", "Ago", "Set", "Out", "Nov", "Dez"];
				/* LGEBR-469 */
				$.fn.datePicker.dates['en'].days = ["Domingo", "Segunda-Feira", "Terça-Feira", "Quarta-feira", "Quinta-Feira", "Sexta-Feira", "Sábado"];
				$.fn.datePicker.dates['en'].daysMin = ["Dom", "Seg", "Ter", "Qua", "Qui", "Sex", "Sab"];
				/*//LGEBR-469 */
				// $.fn.datePicker.dates['en'].days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
				// $.fn.datePicker.dates['en'].daysShort = ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"];
				// $.fn.datePicker.dates['en'].daysMin = ["Su", "Mo", "Tu", "We", "Th", "Fr", "Sa"];
			}else if($('html').data('countrycode') == 'cz'){
				$.fn.datePicker.dates['en'].months = ["leden", "únor", "březen", "duben", "květen", "červen", "červenec", "srpen", "září", "říjen", "listopad", "prosinec"];
				$.fn.datePicker.dates['en'].monthsShort = ["led", "úno", "bře", "dub", "kvě", "čvn", "čvc", "srp", "zář", "říj", "lis", "pro"];
				$.fn.datePicker.dates['en'].daysMin = ["Ne", "Po", "Út", "St", "Čt", "Pá", "So"];
				$.fn.datePicker.dates['en'].dateFormat = "dd/mm/yyyy";
			}else if($('html').data('countrycode') == 'sk'){
				$.fn.datePicker.dates['en'].months = ["január", "február", "marec", "apríl", "máj", "jún", "júl", "august", "september", "october", "november", "december"];
			} // PJTGPEMP-1 add
			// 200421 END 전호근 - format 수정
			$this.datePicker(option);
			$this.addClass('hasDatePicker');
			$this.closest('.datepicker-wrap').find('.datepicker-trigger').on('click', function() {
				$this.focus();
			});
		});
	};
